.videoFooter {
    position: relative;
    background-color: rgba(0,0,0,0.3);
    border-radius: 8px;
    padding: 8px;
    color: white;
    -webkit-transform: translateY(-120%);
        -ms-transform: translateY(-120%);
            transform: translateY(-120%);
    display: -ms-flexbox;
    display: flex;
    margin: 0 2rem;
  }
  
  .videoFooter__text {
    -ms-flex: 1 1;
        flex: 1 1;
  }
  
  .videoFooter__text > p {
    padding-bottom: 20px;
  }
  
  .videoFooter__record {
    -webkit-animation: spinTheRecord infinite 5s linear;
            animation: spinTheRecord infinite 5s linear;
    height: 50px;
    -webkit-filter: invert(1);
            filter: invert(1);
    position: absolute;
    bottom: 0;
    right: 20px;
  }
  
  .videoFooter__icon {
    position: absolute;
  }
  
  .videoFooter__ticker > .ticker {
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    margin-left: 30px;
    width: 60%;
  }
  
  .videoFooter__text > h3 {
    padding-bottom: 20px;
  }
  
  .videoFooter__ticker h1 {
    padding-top: 7px;
  }
  
  @-webkit-keyframes spinTheRecord {
    from {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
    }
    to {
      -webkit-transform: rotate(360deg);
              transform: rotate(360deg);
    }
  }
  
  @keyframes spinTheRecord {
    from {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
    }
    to {
      -webkit-transform: rotate(360deg);
              transform: rotate(360deg);
    }
  }
  


